"use client";
import React, { useState } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { Dropdown } from "../ui/dropdown/Dropdown";
import { DropdownItem } from "../ui/dropdown/DropdownItem";
import { useNotifications } from "@/hooks/useNotifications";
import { useAuth } from "@/hooks/useAuth";
import { formatDate } from "@/utils/format";
import { BellIcon } from "@/icons";
import NotificacaoModal from "@/components/notificacoes/NotificacaoModal";
import type { Notificacao } from "@/services/api/notificacao.service";

function formatTimeAgo(dateString: string): string {
  try {
    const date = new Date(dateString);
    const now = new Date();
    const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000);

    if (diffInSeconds < 60) return 'agora';
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)} min atr√°s`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)} hr atr√°s`;
    if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)} dias atr√°s`;
    
    // Para datas mais antigas, usar formato brasileiro sem horas
    return new Intl.DateTimeFormat('pt-BR', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    }).format(date);
  } catch {
    return dateString;
  }
}

function getTipoIcon(tipo: string): string {
  switch (tipo) {
    case 'VENCIMENTO_EMPRESA':
      return '‚ö†Ô∏è';
    case 'PLANO_PAGO':
      return '‚úÖ';
    case 'COBRANCA_GERADA':
      return 'üí≥';
    case 'FATURA_PAGA':
      return 'üí∞';
    case 'PARCELA_PAGA':
      return 'üíµ';
    case 'MANUAL':
      return 'üì¢';
    default:
      return 'üîî';
  }
}

export default function NotificationDropdown() {
  const router = useRouter();
  const [isOpen, setIsOpen] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedNotificacao, setSelectedNotificacao] = useState<Notificacao | null>(null);
  const { user } = useAuth();
  const {
    notificacoes,
    countNaoLidas,
    loading,
    isConnected,
    marcarComoLida,
    marcarTodasComoLidas,
    deleteNotificacao
  } = useNotifications();
  
  const isSuperAdmin = user?.role === 'SUPERADMIN';

  function toggleDropdown() {
    setIsOpen(!isOpen);
  }

  function closeDropdown() {
    setIsOpen(false);
  }

  const handleNotificationClick = async (notificacao: Notificacao) => {
    // Marcar como lida se n√£o estiver lida
    if (!notificacao.lida) {
      await marcarComoLida(notificacao.id);
    }
    
    // Abrir modal com detalhes da notifica√ß√£o
    setSelectedNotificacao(notificacao);
    setIsModalOpen(true);
    closeDropdown();
  };
  
  const handleActionLink = (link?: string, e?: React.MouseEvent<HTMLSpanElement>) => {
    e?.preventDefault();
    e?.stopPropagation();
    if (!link) return;
    router.push(link);
    closeDropdown();
  };

  const handleMarcarTodasComoLidas = async () => {
    await marcarTodasComoLidas();
  };

  const handleCloseModal = () => {
    setIsModalOpen(false);
    setSelectedNotificacao(null);
  };

  const handleMarcarComoLidaModal = async (id: number) => {
    await marcarComoLida(id);
    // Atualizar a notifica√ß√£o selecionada no estado
    if (selectedNotificacao && selectedNotificacao.id === id) {
      setSelectedNotificacao({ ...selectedNotificacao, lida: true });
    }
  };

  const handleDeleteModal = async (id: number) => {
    if (window.confirm('Tem certeza que deseja excluir esta notifica√ß√£o?')) {
      await deleteNotificacao(id);
      handleCloseModal();
    }
  };

  return (
    <div className="relative">
      <button
        className="relative dropdown-toggle flex items-center justify-center text-gray-500 transition-colors bg-white border border-gray-200 rounded-full hover:text-gray-700 h-11 w-11 hover:bg-gray-100 dark:border-gray-800 dark:bg-gray-900 dark:text-gray-400 dark:hover:bg-gray-800 dark:hover:text-white"
        onClick={toggleDropdown}
        aria-label="Notifica√ß√µes"
        title="Notifica√ß√µes"
      >
        {countNaoLidas > 0 && (
          <span className="absolute right-0 top-0.5 z-10 h-2 w-2 rounded-full bg-orange-400 flex">
            <span className="absolute inline-flex w-full h-full bg-orange-400 rounded-full opacity-75 animate-ping"></span>
          </span>
        )}
        {!isConnected && (
          <span className="absolute right-0 top-0.5 z-10 h-2 w-2 rounded-full bg-gray-400 flex" title="Desconectado"></span>
        )}
        {/* <BellIcon className="w-5 h-5" /> */}
        <svg
          className="fill-current"
          width="20"
          height="20"
          viewBox="0 0 20 20"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fillRule="evenodd"
            clipRule="evenodd"
            d="M10.75 2.29248C10.75 1.87827 10.4143 1.54248 10 1.54248C9.58583 1.54248 9.25004 1.87827 9.25004 2.29248V2.83613C6.08266 3.20733 3.62504 5.9004 3.62504 9.16748V14.4591H3.33337C2.91916 14.4591 2.58337 14.7949 2.58337 15.2091C2.58337 15.6234 2.91916 15.9591 3.33337 15.9591H4.37504H15.625H16.6667C17.0809 15.9591 17.4167 15.6234 17.4167 15.2091C17.4167 14.7949 17.0809 14.4591 16.6667 14.4591H16.375V9.16748C16.375 5.9004 13.9174 3.20733 10.75 2.83613V2.29248ZM14.875 14.4591V9.16748C14.875 6.47509 12.6924 4.29248 10 4.29248C7.30765 4.29248 5.12504 6.47509 5.12504 9.16748V14.4591H14.875ZM8.00004 17.7085C8.00004 18.1228 8.33583 18.4585 8.75004 18.4585H11.25C11.6643 18.4585 12 18.1228 12 17.7085C12 17.2943 11.6643 16.9585 11.25 16.9585H8.75004C8.33583 16.9585 8.00004 17.2943 8.00004 17.7085Z"
            fill="currentColor"
          />
        </svg>
      </button>
      <Dropdown
        isOpen={isOpen}
        onClose={closeDropdown}
        className="absolute -right-[240px] mt-[17px] flex h-[480px] w-[350px] flex-col rounded-2xl border border-gray-200 bg-white p-3 shadow-theme-lg dark:border-gray-800 dark:bg-gray-dark sm:w-[361px] lg:right-0"
      >
        <div className="flex items-center justify-between pb-3 mb-3 border-b border-gray-100 dark:border-gray-700">
          <div className="flex items-center gap-2">
            <h5 className="text-lg font-semibold text-gray-800 dark:text-gray-200">
              Notifica√ß√µes
            </h5>
            {countNaoLidas > 0 && (
              <span className="px-2 py-0.5 text-xs font-semibold text-white bg-orange-500 rounded-full">
                {countNaoLidas}
              </span>
            )}
          </div>
          <div className="flex items-center gap-2">
            {countNaoLidas > 0 && (
              <button
                onClick={handleMarcarTodasComoLidas}
                className="text-xs text-brand-500 hover:text-brand-600 dark:text-brand-400 dark:hover:text-brand-300"
                title="Marcar todas como lidas"
              >
                Marcar todas
              </button>
            )}
            <button
              onClick={closeDropdown}
              className="text-gray-500 transition dropdown-toggle dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-200"
              aria-label="Fechar"
            >
              <svg
                className="fill-current"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  clipRule="evenodd"
                  d="M6.21967 7.28131C5.92678 6.98841 5.92678 6.51354 6.21967 6.22065C6.51256 5.92775 6.98744 5.92775 7.28033 6.22065L11.999 10.9393L16.7176 6.22078C17.0105 5.92789 17.4854 5.92788 17.7782 6.22078C18.0711 6.51367 18.0711 6.98855 17.7782 7.28144L13.0597 12L17.7782 16.7186C18.0711 17.0115 18.0711 17.4863 17.7782 17.7792C17.4854 18.0721 17.0105 18.0721 16.7176 17.7792L11.999 13.0607L7.28033 17.7794C6.98744 18.0722 6.51256 18.0722 6.21967 17.7794C5.92678 17.4865 5.92678 17.0116 6.21967 16.7187L10.9384 12L6.21967 7.28131Z"
                  fill="currentColor"
                />
              </svg>
            </button>
          </div>
        </div>
        
        {loading ? (
          <div className="flex items-center justify-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-brand-500"></div>
          </div>
        ) : notificacoes.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-8 text-center">
            <BellIcon className="w-12 h-12 text-gray-400 mb-2" />
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Nenhuma notifica√ß√£o
            </p>
          </div>
        ) : (
          <ul className="flex flex-col h-auto overflow-y-auto custom-scrollbar">
            {notificacoes.map((notificacao) => (
              <li key={notificacao.id}>
                <DropdownItem
                  onItemClick={() => handleNotificationClick(notificacao)}
                  className={`flex gap-3 rounded-lg border-b border-gray-100 p-3 px-4.5 py-3 hover:bg-gray-100 dark:border-gray-800 dark:hover:bg-white/5 ${
                    !notificacao.lida ? 'bg-blue-50/50 dark:bg-blue-900/10' : ''
                  }`}
                >
                  <span className="relative flex items-center justify-center w-10 h-10 rounded-full bg-gray-100 dark:bg-gray-800 flex-shrink-0">
                    <span className="text-xl">{getTipoIcon(notificacao.tipo)}</span>
                    {!notificacao.lida && (
                      <span className="absolute top-0 right-0 h-2.5 w-2.5 rounded-full bg-blue-500 border-2 border-white dark:border-gray-900"></span>
                    )}
                  </span>

                  <span className="block flex-1 min-w-0">
                    <span className={`mb-1.5 block text-sm ${!notificacao.lida ? 'font-semibold text-gray-900 dark:text-white' : 'text-gray-700 dark:text-gray-300'}`}>
                      {notificacao.titulo}
                    </span>
                    <span className="block text-xs text-gray-500 dark:text-gray-400 line-clamp-2">
                      {notificacao.mensagem}
                    </span>
                    {/* Mostrar empresa e usu√°rio apenas para SUPERADMIN */}
                    {isSuperAdmin && (notificacao.empresa || notificacao.user) && (
                      <span className="block mt-1 text-xs text-gray-400 dark:text-gray-500">
                        {notificacao.empresa && (
                          <span className="mr-2">
                            üì¶ {notificacao.empresa.nome}
                          </span>
                        )}
                        {notificacao.user && (
                          <span>
                            üë§ {notificacao.user.nome}
                          </span>
                        )}
                      </span>
                    )}
                    <span className="flex items-center gap-2 mt-1.5 text-xs text-gray-400 dark:text-gray-500">
                      <span>{formatTimeAgo(notificacao.createdAt)}</span>
                      {notificacao.actionLink && notificacao.actionLabel && (
                        <>
                          <span className="w-1 h-1 bg-gray-400 rounded-full"></span>
                          <span className="text-brand-500 hover:text-brand-600 dark:text-brand-400 cursor-pointer" 
                          onClick={(e: React.MouseEvent<HTMLSpanElement>) => handleActionLink(notificacao.actionLink || undefined, e)}
                          title={notificacao.actionLabel}>
                            {notificacao.actionLabel}
                          </span>
                        </>
                      )}
                    </span>
                  </span>
                </DropdownItem>
              </li>
            ))}
          </ul>
        )}
        
        {notificacoes.length > 0 && (
          <Link
            href={isSuperAdmin ? '/dashboard/admin/notificacoes' : '/dashboard/empresa/notificacoes'}
            className="block px-4 py-2 mt-3 text-sm font-medium text-center text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-100 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-400 dark:hover:bg-gray-700"
            onClick={closeDropdown}
          >
            Ver Todas as Notifica√ß√µes
          </Link>
        )}
      </Dropdown>

      {/* Modal de visualiza√ß√£o */}
      <NotificacaoModal
        isOpen={isModalOpen}
        onClose={handleCloseModal}
        notificacao={selectedNotificacao}
        onMarcarComoLida={handleMarcarComoLidaModal}
        onDelete={handleDeleteModal}
      />
    </div>
  );
}
